import React, { useState, useEffect, useRef } from 'react'
import { motion } from 'framer-motion'
import { observer } from 'mobx-react-lite'
import useWindowWidth from 'use-window-width'
import useScrollPosition from '@react-hook/window-scroll'

import mainStyles from './main.module.css'
import lightStyles from './light.module.css'
import darkStyles from './dark.module.css'
import globalLightStyles from './../../../styles/light.module.css'
import globalDarkStyles from './../../../styles/dark.module.css'

const dark = { ...mainStyles, ...globalDarkStyles, ...darkStyles }
    , light = {  ...mainStyles, ...globalLightStyles, ...lightStyles }

import useCastumStyle from './../../../../../lib/use-castum-style'
import useAudio from './../../../../../lib/use-audio'
import useBodyWidth from './../../../../../lib/use-body-width'
import formatTime from './../../../../../lib/format-time'

import store from './../../../../../store'

import playIcon from './../../../../../assets/icons/play.svg'
import pauseIcon from './../../../../../assets/icons/pause.svg'

const Sound = observer(({ data, isPlay, onPlay }) => {
  const styles = store.theme === 'light'
                  ? light
                  : dark

  const [context, setContext] = useState(null)
  const refCanvas = useRef()
  const refPostWidth = useRef()
  const updWidth = useWindowWidth()
  const updScroll = useScrollPosition(10)

  const width = useBodyWidth(refPostWidth, updWidth, updScroll)
      , canvasWidth = width > 640 ? width - 50 - 48 - 15 - 10 : width - 50 - 24 - 15 - 10

  const audio = useAudio(data.sound)

  useEffect(() => {
    audio.setPlay(isPlay)
  }, [isPlay])

  useEffect(() => {
    if (isPlay !== audio.isPlay) {
      onPlay(audio.isPlay)
    }
  }, [audio.isPlay])

  useEffect(() => {
    const nodeCanvas = refCanvas.current

    if (nodeCanvas) {
      let isDown = false

      const handlerMoveStart = (e) => {
        isDown = true
        handlerMove(e)
      }

      const handlerMoveEnd = () => {
        isDown = false
      }

      const handlerMove = (e) => {
        e.preventDefault()
        if (isDown) {
          const currentTime = parseInt((e.offsetX / canvasWidth) * audio.duration)
          audio.setCurrentTime(currentTime)
        }
      }

      nodeCanvas.addEventListener('mousedown', handlerMoveStart)
      nodeCanvas.addEventListener('mouseup', handlerMoveEnd)
      nodeCanvas.addEventListener('mouseleave', handlerMoveEnd)
      nodeCanvas.addEventListener('mousemove', handlerMove)

      return () => {
        nodeCanvas.removeEventListener('mousedown', handlerMoveStart)
        nodeCanvas.removeEventListener('mouseup', handlerMoveEnd)
        nodeCanvas.removeEventListener('mouseleave', handlerMoveEnd)
        nodeCanvas.removeEventListener('mousemove', handlerMove)
      }
    }
  }, [refCanvas.current, canvasWidth, audio.duration])

  useEffect(() => {
    const nodeCanvas = refCanvas.current

    if (nodeCanvas) {
      const context = nodeCanvas.getContext('2d')
      setContext(context)
    }
  }, [refCanvas.current, audio.audioBuffer, canvasWidth])

  useEffect(() => {
    if (context && audio.audioBuffer) {
      context.clearRect(0, 0, canvasWidth, 25)

      const chunk = parseInt(audio.audioBuffer.length / canvasWidth)

      const progress = audio.currentTime / audio.duration

      for (let x = 0; x < canvasWidth; x += 6) {
        context.fillStyle = parseInt(progress * canvasWidth) >= x + 6
                              ? store.theme === 'light'
                                  ? '#333333'
                                  : '#f3f3f3'
                              : store.theme === 'light'
                                  ? '#dddddd'
                                  : '#888888'

        const wave = (Math.abs(audio.audioBuffer[chunk * x]) * 30) + 2
        context.fillRect(x, 0, 3, wave)
      }
    }
  }, [context, audio.currentTime, audio.audioBuffer, audio.duration, canvasWidth, store.theme])

  return (
    <div
      className={styles.tapeBody}
      ref={refPostWidth}
    >
      <div className={styles.overflow}>
        <div
          className={styles.circleButton}
          onClick={
            () => onPlay(!isPlay)
          }
        >
          <motion.div
            className={styles.icon}
            style={{
              marginLeft: isPlay ? '1px' : '0px'
            }}
            animate={
              {
                backgroundImage: `url(${isPlay ? pauseIcon : playIcon})`,
                scale: isPlay ? -1 : 1
              }
            }
          />
        </div>
        <div className={styles.container}>
          {
            isPlay
              ? (
                <canvas ref={refCanvas} style={{ transform: 'rotateX(180deg)' }} height='25' width={canvasWidth - 10} />
              )
              : (
                <a className={styles.title} style={{ height: '20px' }}>{(d => d.length === data.title.length ? d : d+'...')(data.title.slice(0, parseInt(canvasWidth / 10)))}</a>
              )
          }
          <div className={styles.timeContainer}>{formatTime(audio.currentTime)} / {formatTime(audio.duration)}</div>
        </div>
      </div>
    </div>
  )
})

const SoundTape = observer(({ items }) => {
  useCastumStyle()

  const styles = store.theme === 'light'
                  ? light
                  : dark

  const [_items, setItems] = useState(
    items.map(item => ({ ...item, isPlay: false }))
  )

  useEffect(() => {
    setItems(
      items.map(item => ({ ...item, isPlay: false }))
    )
  }, [items.map(e => e.title).join('')])

  return (
    <div className={styles.body}>
      <div className={styles.wrapper}>
        {
          _items.map((item, key) =>
            <Sound
              key={key}
              data={item}
              isPlay={item.isPlay}
              onPlay={
                isPlay =>
                  setItems(
                    items =>
                      items.map(
                        (item, _key) => ({ ...item, isPlay: key === _key ? isPlay : false })
                      )
                  )
              }
            />
          )
        }
      </div>
    </div>
  )
})

export default SoundTape
