const formatTime = second => {
  let hours = parseInt(second / 3600)
    , minutes = parseInt((second - (hours * 3600)) / 60)
    , seconds = parseInt(second - (hours * 3600) - (minutes * 60))

  if (hours < 10) {
    hours = '0' + hours
  }

  if (minutes < 10) {
    minutes = '0' + minutes
  }

  if (seconds < 10) {
    seconds = '0' + seconds
  }

  return hours + ':' + minutes + ':' + seconds
}

export default formatTime
