import { useEffect, useState } from 'react'

const useBodyWidth = (ref, ...update_vars) => {
  const [width, setWidth] = useState(0)

  useEffect(() => {
    const nodeBody = ref.current

    if (nodeBody) {
      const { width } = nodeBody.getBoundingClientRect()
      setWidth(width)
    }
  }, [ref, update_vars])

  return width
}

export default useBodyWidth
