import { useEffect, useState } from 'react'

const useAudio = (src) => {
  const [audioNode, setAudioNode] = useState(null)
      , [audioBuffer, setAudioBuffer] = useState(null)
      , [currentTime, setCurrentTime] = useState(0)
      , [duration, setDuration] = useState(0)
      , [isPlay, setPlay] = useState(false)

  useEffect(() => {
    const timeId = setTimeout(() => {
      const audio = new Audio()
      audio.src = src
      audio.volume = 1
      setAudioNode(audio)
    })

    return () => clearTimeout(timeId)
  }, [src])

  useEffect(() => {
    const timeId = setTimeout(() => {
      try {
        const audioCtx = 'webkitAudioContext' in window
                              ? (new window.webkitAudioContext())
                              : (new AudioContext())

        const source = audioCtx.createBufferSource()

        fetch(src)
          .then(response => response.arrayBuffer())
          .then(arrayBuffer => {
            audioCtx.decodeAudioData(
              arrayBuffer,
              buffer => {
                source.buffer = buffer
                setAudioBuffer(source.buffer.getChannelData(0))
              },
              err => console.log(err)
            )
          }).catch(err => console.log(err))
      } catch (err) {
        console.log(err)
      }
    }, 1000)

    return () => clearTimeout(timeId)
  }, [src])

  useEffect(() => {
    const timeId = setTimeout(() => {
      const audio = audioNode

      if (audio && !currentTime.toString().match(/\./)) {
        audio.currentTime = currentTime
      }
    })

    return () => clearTimeout(timeId)
  }, [audioNode, currentTime])

  useEffect(() => {
    const audio = audioNode

    if (audio) {
      const handlerEnded = () => setPlay(false)
          , handlerTimeupdate = () => {
              if (audio.currentTime.toString().match(/\./)) {
                setCurrentTime(audio.currentTime)
              }
            }
          , handlerLoadedmetadata = () => setDuration(audio.duration)

      audio.addEventListener('timeupdate', handlerTimeupdate)
      audio.addEventListener('loadedmetadata', handlerLoadedmetadata)
      audio.addEventListener('ended', handlerEnded)

      return () => {
        audio.removeEventListener('timeupdate', handlerTimeupdate)
        audio.removeEventListener('loadedmetadata', handlerLoadedmetadata)
        audio.removeEventListener('ended', handlerEnded)
      }
    }
  }, [audioNode, currentTime])

  useEffect(() => {
    const timeId = setTimeout(() => {
      const audio = audioNode
      if (audio) {
        if (isPlay) {
          try {
            audio.play()
          } catch (err) {
            console.log(err)
            alert('Попробуйте через несколько секунд, пока загружается..')
          }
        } else {
          try {
            audio.pause()
          } catch (err) {
            console.log(err)
          }
        }
      }
    })

    return () => clearTimeout(timeId)
  }, [isPlay, audioNode])

  return {
    node: audioNode,
    audioBuffer,
    currentTime,
    setCurrentTime,
    duration,
    setPlay,
    isPlay
  }
}

export default useAudio
